@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');


*,
*:before,
*:after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Poppins', sans-serif;
}

.container {
    width: 100%;
    min-height: 100vh;
    background: linear-gradient(135deg, #153677, #4e085f);
    padding: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.todo-app {
    width: 100%;
    max-width: 540px;
    background-color: #fff;
    padding: 40px 30px 70px;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: left;
    gap: 2rem;
}

.app-title {
    display: flex;
    gap: 1rem;
    align-items: center;
}

.app-title h2,
.app-title i {
    color: #002765;
}

.app-title i {
    font-size: 26px;
}

.row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #edeef0;
    border-radius: 30px;
}

input {
    width: 100%;
    border: none;
    outline: none;
    background-color: transparent;
    padding: 12px;
    font-size: 17px;
    padding-left: 20px;
}

button {
    border: none;
    cursor: pointer;
    outline: none;
    padding: 10px 10px;
    border-radius: 30px;
    background-color: #ff5945;
    color: #fff;
    font-size: 18px;
    transition: all .25s ease;
   
}
.btn-disabled{
    filter: brightness(.8);
}

button:hover {
    filter: brightness(.8);
}

@media screen and (max-width: 400px) {
    button {
        padding: 15px;
        border-radius: 0 30px 30px 0;
    }
}

ul {
    list-style: none;
    
}

ul li {
    font-size: 17px;
    padding: 12px 8px 12px 10px;
    margin-top: 5px;
    user-select: none;
    cursor: pointer;
    position: relative;
    display: flex;
}

